@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap");

.media {
  display: flex;
  flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.media:hover {
  background-color: #fff;
  color: #000;
}

.poster {
  border-radius: 10px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}
.subTitle {
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
}

@media (max-width: 550px) {
  .media {
    width: 46%;
  }
}
