@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
* {
  margin: 0;
}
.header {
  width: 100%;
  cursor: pointer;
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-transform: uppercase;
  background-color: #39445a;
  font-family: "Montserrat", sans-serif;
  font-size: 4.7vw;
  padding: 10px 0 15px;
  box-shadow: 0px 1px 5px #000;
  color: #fff;
  z-index: 100;
}

@media (max-width: 1000px) {
  .header {
    padding-top: 15px;
    font-size: 6.4vw;
  }
}

* {
  scroll-behavior: smooth;
}

*::selection {
  background-color: #fff;
  color: #39445a;
}

.app {
  min-height: 100vh;
  background-color: #39445a;
  padding-top: 130px;
  padding-bottom: 70px;
  color: #fff;
}

@media (max-width: 700px) {
  .app {
    padding-top: 70px;
  }
}

.pageTitle {
  text-transform: uppercase;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: #fff;
  margin: 10px 0;
  font-family: "Montserrat", sans-serif;
}

@media (max-width: 1000px) {
  .pageTitle {
    font-size: 6.4vw;
  }
}

.media {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 200px;
  padding: 5px;
  margin: 5px 0;
  background-color: #282c34;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.media:hover {
  background-color: #fff;
  color: #000;
}

.poster {
  border-radius: 10px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}
.subTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-bottom: 5px;
}

@media (max-width: 550px) {
  .media {
    width: 46%;
  }
}

.ContentModal {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}
.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Roboto", sans-serif;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  font-weight: 300;
}

.tagline {
  padding-bottom: 10px;
  -webkit-align-self: center;
          align-self: center;
}

.ContentModal__title {
  height: 12%;
  font-size: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.ContentModal__description {
  display: -webkit-flex;
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
  line-height: 1.5;
}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: -webkit-flex;
    display: flex;
    width: 38%;
  }
  .ContentModal {
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 40px;
  }
  .ContentModal__description {
    font-size: 20px;
  }
  .tagline {
    font-size: 20px;
  }
}

.carouselItem {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  object-fit: contain;
  padding: 10px;
}

.carouselItem__img {
  border-radius: 10px;
  margin-bottom: 5px;
  box-shadow: 0 0 5px #000;
}
.carouselItem__txt {
  font-size: 13px;
  text-align: center;
}

.trending {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

